import React from "react"
import { Button } from "reactstrap"
import styled from "styled-components"
import i18next from "i18next"
const WrapUI = styled.div`
  & > .header {
    font-weight: bold;
    color: #5e6e82;
  }
  & > .body {
    padding: 5px 0px;
  }
  & > .footer {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
    padding-top: 5px;
  }
  & > .footer > button {
    margin-left: 5px;
  }
`
export default function MUIATFilter(props) {
  const { title, msg, onOK, onCancel, colorOK, styleBody } = props
  return (
    <WrapUI className="mui-msgbox_confirm">
      {title && (
        <div className="header" style={{ borderBottom: "1px solid #f1f4f6" }}>
          {title}
        </div>
      )}
      <div className="body" style={styleBody}>
        {msg}
      </div>
      <div className="footer">
        <Button onClick={onCancel} color="secondary" outline>
          {i18next.t("btn.Dismiss")}
        </Button>
        <Button onClick={onOK} color={colorOK || "success"}>
          {i18next.t("btn.OK")}
        </Button>
      </div>
    </WrapUI>
  )
}
